@import url('https://fonts.googleapis.com/css?family=Rouge+Script|Seaweed+Script&display=swap');

.menu{
	height: 140px;
	width: 200px;
	background-color: transparent;
	border-radius: 50%;
	margin-top: 260px;
	box-shadow: 5px 5px 10px black;
	transition-property: height, width, border-radius, margin-top;
	transition-duration: 0.5s;
}	

.menu:hover{
	margin-top: 210px;
	height: 200px;
	width: 300px;
	border-radius: 5%;
	background: url('menu.png') no-repeat center;
}

.bluck{
	display: block;
}

.menu:hover .bluck {
	display: none;
}

.sizez{
	font-weight: 900;
	font-size: 50px;
	font-family: 'Rouge Script', cursive;
 	text-shadow: 10px 10px 10px black;
}

@media (max-width: 700px) {
	.menu{
		width: 70%;
		margin-top: 170px;
	}
	.menu:hover{
		margin-top: 170px;
		height: 200px;
		width: 80%;
		border-radius: 5%

	}
}
