@import url('https://fonts.googleapis.com/css?family=Rouge+Script|Seaweed+Script&display=swap');

.forme{
	height: 140px;
	width: 200px;
	background-color: transparent;
	border-radius: 50%;
	margin-top: 260px;
	box-shadow: 5px 5px 10px black;
	transition-property: height, width, border-radius, margin-top;
	transition-duration: 0.5s;

}

.forme:hover{
	margin-top: 150px;
	height: 450px;
	width: 300px;
	border-radius: 5%;

}
.black{
	color: black;
}	

.blick{
	display: none;
}

.forme:hover .blick {
	display: block;
}

.siz{
	font-weight: 900;
	font-size: 50px;
	font-family: 'Rouge Script', cursive;
 	text-shadow: 10px 10px 10px black;
}

@media (max-width: 700px) {
	.forme{
		width: 70%;
		margin-top: 20px;
		margin-bottom: 30px;
	}
	.forme:hover{
		margin-top: 20px;
		height: 450px;
		width: 80%;
		border-radius: 5%;

	}
}

